body {
  font-family: 'Merriweather', serif;
  z-index: 0;
}

.empty{
  background: url('/empty-map.png');
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
}

aside {
  position: fixed;
  width: 20%;
  height: 100vh;
  background: #1c2c4e;
  z-index: 99999;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transition: all 3s;
  color : #ecf0f1;
  /* box-shadow: 2px 0px 5px 0px rgba(0,0,0,0.75); */
}

aside .alert {
  width: 90%;
}
aside form  {
  width: 100%;
  text-align: center;
}

aside form div input{
  margin: 10px;
}

/* aside h3 {
  color : #ecf0f1;
} */

.min-aside {
  width: 20%;
}

aside > div {
  text-align: left;
} 

aside .link {
  color : #00d3c1;
  font-size: 15px;
  text-align: center;
  margin: 0;
}

.login  {
  color : #00d3c1;
}

.login button {
  color: #1c2c4e;
  background-color: #00d3c1 ;
  cursor: pointer;
  border-radius:5px;
  padding: 2px;
  width: 200px;
  height: 50px;
}

.login button div {
  margin: 3px;
  border: 1px #1c2c4e;
  border-radius: 5px;
}

.navbar .navbar-brand {
  
  color: #1c2c4e;
  background-color: #00d3c1 ;
  cursor: pointer;
  border-radius:5px;
  padding: 2px;
  
}

.navbar .navbar-brand div {
  margin: 3px;
  border: 1px #1c2c4e;
  border-radius: 5px;
}

.navbar {
  color: #00d3c1;
}

.logout {
  cursor: pointer;
}
.float{
  height: 60px;
  width: 50px;
	position:fixed;
  top: 0;
  left: 0;
	/* background-color:#2980b9; */
	color: #ecf0f1;
	border-radius:50px;
  text-align:center;
	/* box-shadow: 2px 2px 3px #999; */
}

.my-float{
	margin-top:22px;
}

.home {
  height: 100vh;
}

.home-margin {
  margin-left: 20%;
}

thead {
  background-color: #1c2c4e;
  color: #00d3c1;

} 


.leaflet-container {
  margin-left: 50%;
  height: 95vh;
  width: 100%;
  margin: 0 auto;
}

.float-button {
  margin: 10px;
	width: 60px;
	height: 30px;
	background-color:#00d3c1;
	color:#1c2c4e;
	border-radius:50px;
	text-align:center;
  font-size: 20px;
  /* display: inline-block; */
}

.popup-button {
  display: inline-block;
}

.icon {
  width: 38;
  height: 40;
  margin: 2px;
}

.instructions ol {
  margin: 10px;
}
.instructions h5, .instructions h3 {
  text-align: center;
  margin-top: 10px;
  color: #00d3c1;
}

button:disabled {
  background-color: #bdc3c7;
}

.float-second-icon {
  font-size: 15px;
}

.map {
  box-shadow: 0px 4px 5px 0px rgba(0,0,0,0.75);
}

.map li {
  border-radius: 5px;
}

nav {
  background-color: #1d315b;
}

nav a{
  font-size: 15px;
  color: #00d3c1;
}

button {
  border: 0;
  padding: 0;
  margin: 10px;
	width:60px;
	height:30px;
	background-color:#00d3c1;
	color:#1c2c4e;
	border-radius:50px;
	text-align:center;
  font-size: 20px;
  display: inline-block;
}


@media (max-width : 1073px) {
  aside {
    width: 40%;
  }
  .home-margin {
    margin-left: 40%;
  }
}

@media (max-width : 1589px) {
  aside {
    width: 30%;
  }
  .home-margin {
    margin-left: 30%;
  }
}

@media (max-width : 768px) {
  aside {
    width: 50%;
  }
  .home-margin {
    margin-left: 50%;
  }
}



@media (max-width : 701px) {
  aside {
    width: 100%;
  }
  .home-margin {
    margin-left: 0%;
  }
}





@media (min-width : 701px) {
  .float{
    display: none;
  }
  
  .my-float{
    display: none;
  }
}